}

* {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  outline: 0;
}

body {
  margin: 0;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 16px;
  background-color: #edeef0;
  overflow-x: hidden;
}

body.no-scroll {
  overflow-y: hidden;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  border: 0;
}

button {
  border: 0;
  background-color: transparent;
}

a {
  color: inherit;
  text-decoration: none;
}

input {
  outline: 0;
}

select {
  border-radius: 0;
}

select:-moz-focusring {
  color: transparent;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  line-height: 1.5;
}

.h1 {
  font-size: 34px;
  font-weight: 700;
}

.h2 {
  font-size: 50px;
  font-weight: 300;
  line-height: 1.2;
}

.h3 {
  font-size: 30px;
  font-weight: 400;
}

.h4 {
  font-size: 14px;
  font-weight: 700;
}

.link {
  color: #0c4384;
}

.link:hover {
  text-decoration: underline;
}

.dropdown-block {
  position: relative;
}

.dropdown-block .dropdown-menu {
  position: absolute;
  padding: 15px 0;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 9px 0 rgba(0, 0, 0, 0.5);
  transition: opacity 100ms linear, top 100ms linear, visibility 100ms linear;
  z-index: 800;
}

.dropdown-block .dropdown-menu.active {
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.dropdown-block .dropdown-menu .drop-item {
  padding: 12px 25px;
  font-size: 14px;
  color: #0e1824;
  cursor: pointer;
}

.dropdown-block .dropdown-menu .drop-item:hover {
  background-color: #edeef0;
}

.dropdown-block .drop-menu-currency {
  width: 220px;
  top: -45px;
}

.dropdown-block .dropdown-menu-user {
  width: 192px;
  top: 25px;
  right: 0;
}

.dropdown-block .dropdown-menu-lang {
  width: 192px;
  top: -15px;
}

.panel {
  margin-bottom: 20px;
  background-color: #fff;
  border: solid 1px #e3e4e8;
  border-radius: 4px;
}

.panel .panel-title {
  padding: 15px 30px;
  font-size: 17px;
  font-weight: 400;
