}

.overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  min-width: 1260px;
}

.overlay.waves-overlay {
  visibility: visible;
  opacity: 1;
  pointer-events: all;
  background-color: rgba(255, 255, 255, 0.72);
  z-index: 1000;
}

.overlay.waves-overlay.hide {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.overlay.popup-overlay {
  background-color: rgba(0, 0, 0, 0.8);
  pointer-events: none;
  opacity: 0;
  z-index: 2000;
  transition: all .2s ease-in;
  cursor: pointer;
  visibility: hidden;
}

.overlay.popup-overlay.visible {
  opacity: 1;
  pointer-events: all;
  overflow-y: scroll;
  visibility: visible;
}

.popup {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 660px;
  margin: 40px 0 40px;
  padding: 20px 25px;
  font-size: 15px;
  color: #0e1824;
  line-height: 1.6;
  background-color: #fff;
  border-radius: 4px;
  opacity: 0;
  pointer-events: none;
  transition: all 0.2s ease-in;
  cursor: default;
}

.popup.visible {
  opacity: 1;
  pointer-events: all;
  z-index: 2100;
}

.popup .pp-title {
  font-size: 30px;
  line-height: 50px;
  font-weight: normal;
  text-align: center;
}

.popup .pp-desc {
  margin-bottom: 40px;
  padding: 0 50px;
  font-size: 17px;
  line-height: 1.41;
  text-align: center;
  color: #808080;
}

.popup .pp-desc-invest {
  font-size: 30px;
  font-weight: normal;
  color: #0e1824;
}

.popup .pp-warrning {
  margin-bottom: 30px;
}

.popup .pp-warrning .title {
  font-weight: bold;
  color: #e82a2a;
}

.popup .pp-link {
  color: #3854f3;
}

.popup .pp-link:hover {
  text-decoration: underline;
}

.popup .pp-manual-list .pp-manual-item {
  margin-bottom: 30px;
}

.popup .pp-manual-list .pp-manual-item span {
  display: block;
  margin-bottom: 20px;
}

.popup .pp-manual-list .pp-manual-item .red {
  font-style: normal;
  color: #e82a2a;
}

.popup .pp-manual-list .pp-manual-item .pp-warrning {
  margin-top: 40px;
}

.popup .pp-img {
  display: block;
  width: 90%;
  height: 333px;
  margin: 30px auto;
}

.popup .popup-close {
  position: absolute;
  top: 6px;
  right: -38px;
  width: 18px;
  height: 18px;
  background: url("../img/icons/ic-close.svg") no-repeat center;
  cursor: pointer;
}

.alert-waves {
  position: relative;
  padding: 25px 0;
  background-color: #4f585d;
  z-index: 1500;
}

.alert-waves .waves-address-wrap {
  position: relative;
  float: left;
