}

.alert-waves .waves-address-wrap .waves-desc {
  margin-right: 15px;
  color: #fff;
}

.alert-waves .waves-address-wrap .waves-input {
  width: 450px;
  height: 45px;
}

.alert-waves .waves-address-wrap .waves-btn {
  margin-left: 10px;
  font-size: 15px;
  color: #fff;
  border: solid 1px #fff;
}

.alert-waves .waves-address-wrap .waves-btn:hover {
  color: #4f585d;
  background-color: #fff;
}

.alert-waves .waves-address-wrap .waves-tip {
  position: absolute;
  left: 107px;
  top: 60px;
  right: 0;
  padding: 10px;
  font-size: 15px;
  line-height: 1.53;
  color: #fff;
  border-radius: 2px;
  background-color: #1c252a;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: opacity 100ms linear, right 100ms linear, visibility 100ms linear;
}

.alert-waves .waves-address-wrap .waves-tip.show {
  visibility: visible;
  opacity: 1;
  top: 50px;
  pointer-events: auto;
}

.alert-waves .waves-about {
  float: right;
  padding: 12px 0;
}

.alert-waves .open-popup {
  font-size: 15px;
  color: #76b5ff;
  border-bottom: 1px dashed #76b5ff;
}

.tooltip {
  position: relative;
  top: 1px;
  display: inline-block;
  vertical-align: baseline;
  margin-left: 5px;
}

.tooltip .tooltip-call {
  width: 12px;
  height: 12px;
  cursor: pointer;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.tooltip .tooltip-call.black {
  background-image: url("../img/icons/tip.svg");
}

.tooltip .tooltip-call.gray {
  background-image: url("../img/icons/tip-gray.svg");
}

.tooltip .tooltip-call.white {
  background-image: url("../img/icons/tip-white.svg");
}

.tooltip .tooltip-content {
  position: absolute;
  width: 360px;
  padding: 18px;
  font-size: 15px;
  line-height: 1.53;
  color: #fff;
  border-radius: 2px;
  background-color: #1c252a;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  z-index: 800;
  transition: opacity 100ms linear, right 100ms linear, top 100ms linear, visibility 100ms linear;
}

.tooltip .tooltip-content::after {
  content: '';
  position: absolute;
  right: -18px;
  top: 50%;
  height: 0;
  width: 0;
  margin-top: -5px;
  pointer-events: none;
  border: solid transparent;
  border-width: 9px;
  border-left-color: #1c252a;
}

.tooltip .tooltip-content.show {
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.tooltip.tooltip-bonus .tooltip-content {
  top: 50%;
  right: 37px;
  transform: translateY(-52%);
}

.tooltip.tooltip-bonus .tooltip-content.show {
  right: 27px;
}

.tooltip.tooltip-waves {
  margin-left: 0;
}

.tooltip.tooltip-waves .tooltip-content {
  top: 50%;
  right: 37px;
  transform: translateY(-52%);
  text-align: left;
}

.tooltip.tooltip-waves .tooltip-content.show {
  right: 27px;
}

.tooltip.tooltip-waves-header {
  top: 2px;
}

.tooltip.tooltip-waves-header .tooltip-content {
  top: 35px;
  left: 50%;
  transform: translateX(-51%);
  text-align: left;
}

.tooltip.tooltip-waves-header .tooltip-content::after {
  content: '';
  position: absolute;
  left: 50%;
  top: -13px;
  height: 0;
  width: 0;
  margin-left: -5px;
  pointer-events: none;
  border: solid transparent;
  border-width: 9px;
  border-bottom-color: #1c252a;
}

.tooltip.tooltip-waves-header .tooltip-content.show {
  top: 25px;
}

